.button {
    cursor: pointer;
    border: none;
    background-color: #4ae98a;
    color: #216e40;
    padding: 0.75rem 3rem;
    display: flex;
    justify-content: space-around;
    align-items: center;
    border-radius: 5px;
    font-weight: bold;
    border-bottom: 3px solid transparent;
    transition: border-color 0.05s linear;
}

.button:hover {
    border-bottom: 3px solid #216e40;
}

.button:active{
    color: white;
    background-color: #216e40;
}