.card {
    margin: 3rem auto;
    width: 90%;
    max-width: 75rem;
    border-radius: 6px;
    background-color: #3bc472;
    padding: 1rem;
    text-align: center;
    box-shadow: 0 2px 8px rgba(0, 0, 0, 0.25);
    min-height: 70vh;
  }
  
  .card h1,
  h4 {
    text-align: center;
    color: white;
  }